import React, { Component } from 'react';

class Boxes extends Component {
    render() {
        return (
            <div className={this.props.classes}>
                <img src={this.props.imglink} />
                <h3>{this.props.title} </h3>
                <p>{this.props.description}</p>
            </div>)
    }
}

class Healthcare extends Component {

    render() {
        return (
            <section id="healthcarePlatform">
                <div className="healthcarePlatform">
                    <div className="healthcarePlatformPartleft">
                        <h1>The world's biggest healthcare platform</h1>
                        <p>We work from 6 offices all over the world, bringing Docplanner Group to life in almost 20 countries.</p>
                    </div>
                    <div className="Performances">
                        <Boxes classes="performancebox specialbox"
                            imglink="https://www.docplanner.com/img/flag.png"
                            title="Leader in 10 countries"
                            description="Poland, Turkey, Spain, Italy, Czech Republic, Mexico, Brazil, Colombia, Argentina and Chile" />
                        <Boxes classes="performancebox"
                            imglink="https://www.docplanner.com/img/flag.png"
                            title="Leader in 10 countries"
                            description="Poland, Turkey, Spain, Italy, Czech Republic, Mexico, Brazil, Colombia, Argentina and Chile" />
                        <Boxes classes="performancebox"
                            imglink="https://www.docplanner.com/img/flag.png"
                            title="Leader in 10 countries"
                            description="Poland, Turkey, Spain, Italy, Czech Republic, Mexico, Brazil, Colombia, Argentina and Chile" />
                        <Boxes classes="performancebox"
                            imglink="https://www.docplanner.com/img/flag.png"
                            title="Leader in 10 countries"
                            description="Poland, Turkey, Spain, Italy, Czech Republic, Mexico, Brazil, Colombia, Argentina and Chile" />
                    </div>







                </div>
            </section>
        );
    }
}

export default Healthcare;